import React from 'react';

interface Props {
  locale: string;
  useCases: {
    background_color: string;
    background_color_dark: string;
    title: string;
    title_color: string;
    title_color_dark: string;
    keyword: string;
    keyword_color: string;
    keyword_color_dark: string;
    description: string;
    description_color: string;
    description_color_dark: string;
    cards: {
      title: string;
      image: string;
      slug: string;
    }[];
  };
}

const UseCases = ({ locale, useCases }: Props) => (
  <div
    className={`py-16 md:py-32 bg-${useCases.background_color} dark:bg-${useCases.background_color_dark}`}
  >
    <div className="container">
      <h1
        className={`text-5xl sm:text-6xl md:text-9xl text-${useCases.title_color} dark:text-${useCases.title_color_dark}`}
      >
        {useCases.title}{' '}
        <b className={`text-${useCases.keyword_color} dark:text-${useCases.keyword_color_dark}`}>
          {useCases.keyword}
        </b>
      </h1>
      <p
        className={`text-${useCases.description_color} dark:text-${useCases.description_color_dark}`}
      >
        {useCases.description}
      </p>
      <div className="py-12 md:flex md:justify-between">
        {useCases.cards.map(card => (
          <a
            href={`${locale == 'en' ? '' : locale}${card.slug}`}
            className="h-64 w-52 my-8 mx-auto relative md:h-72 md:w-64 block"
          >
            <h3 className="text-white absolute font-display text-4xl text-center pt-8 md:text-5xl">
              {card.title}
            </h3>
            <img src={card.image} className="object-cover h-full rounded shadow" />
          </a>
        ))}
      </div>
    </div>
  </div>
);

export default UseCases;
