import React from 'react';

import Layout from '../components/layout';
import SEO from '../components/seo';
import Hero from '../components/homepage/hero';
import Stats from '../components/homepage/stats';
import Section from '../components/homepage/section';
import UseCases from '../components/homepage/use_cases';

import content from '../../site/content/homepage.json';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
}

const Home = ({ pageContext }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  return (
    <>
      <SEO
        title={content[locale].title}
        description={content[locale].intro}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div id="homepage-content">
          <Hero
            locale={locale}
            title={content[locale].title}
            intro={content[locale].intro}
            news={content[locale].news}
          />
          {content[locale].stats && <Stats stats={content[locale].stats} />}
          {content[locale].sections &&
            content[locale].sections.map(section => <Section section={section} />)}
          {/* USE CASES PART */}
          {content[locale].use_cases && (
            <UseCases locale={locale} useCases={content[locale].use_cases} />
          )}
        </div>
      </Layout>
    </>
  );
};

export default Home;
